.modal-share {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
  background-color: rgba(128, 128, 128, 0.5);
}

.modal-content {
  font-family: "Lato", "sans serif";
  margin: auto;
  margin-top: 15vh;
  text-align: left;
  color: var(--black);
  background-color: var(--white);
  width: min(80%, 35rem);
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 5px;
  padding-bottom: 3px;
  border-radius: 3px;
  filter: drop-shadow(3px 3px 5px var(--gray));
}

.modal-content h3 {
  font-size: 1.5rem;
  margin-top: 10px;
  margin-bottom: 0;
}

.modal-content p {
  text-align: left;
  font-weight: 400;
  font-size: 1.3rem;
}

p.share-text {
  margin-bottom: 15px;
}
.share-element {
  display: flex;
  justify-content: center;
  height: auto;
  vertical-align: middle;
  margin-bottom: 10px;
}

.share-element .share-data {
  text-align: left;
  font-family: "Fira Code", "monospace";
  font-weight: 700;
  margin: unset;
  border: 1px solid var(--gray);
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: var(--stats-font-size);
  border-radius: 3px 0px 0px 3px;
  background-color: var(--half-tone-gray);
}

.share-element .copy-share-element {
  font-family: "Fira Code", "monospace";
  font-weight: 700;
  border: 1px solid var(--gray);
  border-left: 0;
  border-radius: 0px 3px 3px 0px;
  padding: 0 5px 0px 5px;
  display: flex;
  align-items: center;
}

.socials {
  margin: auto;
  padding-bottom: 10px;
}

.socials-carousel {
  display: flex;
  justify-content: center;
}

.socials button {
  float: none;
  font-size: 2.5rem;
}

.socials svg {
  padding: 5px;
}

.copy-share-element button.copy-button {
  font-size: 1.3rem;
  padding: unset;
}

svg {
  color: var(--black);
  margin: 0 auto;
  display: block;
}

svg:hover {
  color: var(--green);
  filter: drop-shadow(0 0 5px var(--green));
}
