.sidebar {
  height: 100%; /* Full-height: remove this if you want "auto" height */
  width: max(9ch, 33%); /* Set the width of the sidebar */
  position: fixed; /* Fixed Sidebar (stay in place on scroll) */
  z-index: 1; /* Stay on top */
  top: 0; /* Stay at the top */
  left: 0;
  margin-top: 1em;
  overflow-x: hidden; /* Disable horizontal scroll */
  font-weight: 700;
  font-size: var(--font-size);
}

/* The navigation menu links */
.sidebar p {
  text-align: right;
  font-weight: 700;
  margin: auto;
  color: var(--black);
}

button {
  font-family: "Fira Code";
  border: none;
  background: none;
  font-weight: 700;
  font-size: var(--font-size);
  color: var(--black);
  float: right;
  padding-top: 0;
  padding-bottom: 0;
  padding-right: 0;
  margin: 0;
  outline: none;
}

button:hover {
  color: var(--green);
}

button,
button:active,
button:focus {
  outline: none;
}
